import { Component } from 'react';
import { Grading } from './Grading';
import { NonGrading } from './NonGrading';

class MainBody extends Component {
  constructor() {
    super();
    this.state = {};
  }

  ChangeScheme = (scheme = 'grading') => {
    if (scheme === 'grading') {
      document.getElementById('Grading').style.display = 'block';
      document.getElementById('NonGrading').style.display = 'none';
      this.props.updateTitle('Grading Scheme');
    } else {
      document.getElementById('NonGrading').style.display = 'block';
      document.getElementById('Grading').style.display = 'none';
      this.props.updateTitle('Non-Grading Scheme');
    }
  };

  render() {
    return (
      <div>
        <div className="container mt-5">
          <div className="row">
            <div className="col-12 col-md-2">
              <p className="h5 mt-2 text-center mb-2">Select Your Scheme: </p>
            </div>
            <div className="col-6 col-md-5">
              <button
                className="btn btn-primary w-100  mt-1"
                type="button"
                onClick={() => {
                  this.ChangeScheme('grading');
                }}
              >
                Grading
              </button>
            </div>
            <div className="col-6 col-md-5">
              <button
                className="btn btn-danger w-100  mt-1"
                type="button"
                onClick={() => {
                  this.ChangeScheme('non-grading');
                }}
              >
                Non-Grading
              </button>
            </div>
          </div>
        </div>

        <div id="Grading">
          <Grading />
        </div>
        <div id="NonGrading">
          <NonGrading />
        </div>
      </div>
    );
  }
}

export { MainBody };
